import React, {useEffect, useState, } from 'react'
import { Firebase } from '../Firebase/config' 
import {BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { Redirect } from "react-router-dom";
import "./SignUp.css"
function SignIn() {
    let fbt, rbt, sbt
    const [state, setstate] = useState('signin')
    const [currentUser, setCurrentUser] = useState(null)
    const [loading, setLoading] = useState(true);

    if (state === 'new') {
        
        sbt = true; fbt = true; rbt = false;
    }
    else if (state === 'signin') {
        
        sbt = false; fbt = true; rbt = true;
    }
    else if (state === 'forgot') {
        
        sbt = true; fbt = false; rbt = true;
    }
    useEffect(() => {
        Firebase.auth().onAuthStateChanged((user) => {
          setCurrentUser(user);
          setLoading(false);
        });
      }, []);
      if (loading) {
        return <p>Loading...</p>;
      }
    const handleSubmit = (e) => {
        e.preventDefault();    
        const { email, password } = e.target.elements;
        try {
          Firebase.auth().createUserWithEmailAndPassword(email.value, password.value);      
          setCurrentUser(true);
        } catch (error) {
          alert(error);
        }
      };

      const handleSubmit2 = (e) => {
        e.preventDefault();
        const { email, password } = e.target.elements;
        try {
          Firebase.auth().signInWithEmailAndPassword(email.value, password.value);
          
        } catch (error) {
          alert(error);
          
        }
      };
      const handleSubmit3 = (e) => {
        e.preventDefault();
        const { email } = e.target.elements;
        Firebase.auth().sendPasswordResetEmail(email).then(function() {
            // Email sent.
            alert("email send")
          }).catch(function(error) {
            // An error happened.
          });
    }
      if (currentUser) {
        return <Redirect to="/" />;
    }
    return (
        <Router>
            
            <body>
                <div className="container">
                    <div className="header">
                        <img className="loginicon" src="./images/login-icon.png" alt="" />
                    </div>
                    <div class="main">
                    <Switch>
                    <Route path="/signin"><form onSubmit={handleSubmit2}>
                            <span>
                                <i className="fa fa-user"></i>
                                <input type="text" placeholder="Username" name="email"  />
                            </span><br />
                            <span>
                                <i className="fa fa-lock"></i>
                                <input type="password" placeholder="Password" name="password" />
                            </span><br />
                            <button  type="submit">Sign In</button>

                        </form></Route>
                        <Route path="/signup"><form onSubmit={handleSubmit}>
                            {/* <span>
                                <i className="fa fa-user"></i>
                                <input type="text" placeholder="Full Name" name="" />
                            </span><br /> */}
                            <span>
                                <i className="fa fa-envelope"></i>
                                <input type="text" placeholder="Email ID" name="email" id="email" required/>
                            
                            </span><br />
                            <span>
                                <i className="fa fa-lock"></i>
                                <input type="password" placeholder="Password" name="password" id="Password" required/>
                            </span><br />
                            
                            <button  type="submit" >Sign Up</button>


                        </form></Route>
                        <Route path="/forgot">
                        <form onSubmit={handleSubmit3}>
                        <span>
                                <i className="fa fa-envelope"></i>
                                <input type="text" placeholder="Email ID" name="email" id="email" />
                            
                            </span><br />
                        <button  type="submit" >forgot</button>
                        </form>
                        </Route>
                        </Switch>
                        <h5 className="h5">
                        <Link className="forgot" to="/forgot"> {fbt && <span  onClick={() => setstate('forgot')}>Fotgot Password?   </span>}</Link>
                        <Link  className="register" to="/signup">{rbt && <span onClick={() => setstate('new')}>  Register New Account</span>}</Link>
                        <Link  className="register" to="/signin">{sbt && <span  onClick={() => setstate('signin')}> Sign In</span>}</Link>
                        
                        </h5>
                        

                    </div>
                </div>
                           
            </body>
        </Router>
    )
}

export default SignIn
